import React from "react";

type Props = {
  src: string;
  size?: number;
  isAmp?: boolean;
};

const Avatar = ({ src, size = 100, isAmp = false }: Props) =>
  isAmp ? (
    <amp-img
      style={{ width: size, height: size, borderRadius: size / 2 }}
      src={src}
      width={size}
      height={size}
    />
  ) : (
    <img
      style={{ width: size, height: size, borderRadius: size / 2 }}
      src={src}
    />
  );
export default Avatar;
